<template>
  <div>
    <h1 v-if="submitted">Thank you for submitting! :-)</h1>
    <FormHelper>
      <div slot="form-title">
        <h1 style="text-align:center">Form title</h1>
      </div>
      <div slot="form-fields">
        <input v-model="posts.user_id" type="number" placeholder="User ID" />
        <input v-model="posts.title" type="text" placeholder="title" />
        <textarea v-model="posts.body" placeholder="Body"></textarea>
        <div :class="{checkbox:true}">
          <label for="checkboxone">New Player</label>
          <input id="checkboxone" type="checkbox" value="new" v-model="posts.checking" />

          <label for="checkboxtwo">Old Player</label>
          <input id="checkboxtwo" type="checkbox" value="old" v-model="posts.checking" />

          <label for="checkboxthree">Beginner</label>
          <input id="checkboxthree" type="checkbox" value="beginner" v-model="posts.checking" />
        </div>
        <h4 v-if="posts.checking.length" style="text-align:center;">{{posts.checking}}</h4>

        <select v-model="posts.author">
          <option v-for="(author,index) in authors" :key="index">{{author}}</option>
        </select>
        <h4>{{posts.author}}</h4>
      </div>

      <div slot="form-controls">
        <button v-on:click.prevent="handleSubmit">Submit</button>
      </div>
    </FormHelper>
  </div>
</template>

<script>
import FormHelper from "./FormHelper.vue";
export default {
  data: function() {
    return {
      posts: {
        user_id: null,
        title: "",
        body: "",
        checking: [],
        author: ""
      },
      authors: ["Lwin", "Maung", "Sighefio"],
      submitted: false
    };
  },
  components: {
    FormHelper
  },
  methods: {
    handleSubmit: function() {
      console.log(this.posts);
      this.$http
        .post("http://jsonplaceholder.typicode.com/posts", {
          urerId: this.posts.user_id,
          title: this.posts.title,
          body: this.posts.body
        })
        .then(() => {
            
          this.submitted = true;
        });
    }
  }
};
</script>

