<template>
  <div id="single-blog">
      <h1>{{post.title}}</h1>
      <article><p>{{post.body}}</p></article>
  </div>
</template>

<script>
export default {
    data:function(){
        return{
            id:this.$route.params.id,
            post:{}
        }
    },
    created:function(){
        this.$http.get('http://jsonplaceholder.typicode.com/posts/'+this.id).then(
            data=>{this.post = data.body}
        )
    }
}
</script>

