<template>
  <div>
    <input v-model="searchs" type="text" placeholder="Search" />
    <div
      v-bind:class="{'post':true}"
      v-for="(post,index) in SearchFilter"
      v-on:click="post.userId=!post.userId"
      :key="index"
    >
      <h1 ><router-link v-rainbow :to="'/'+post.id"> {{post.title | to-uppercase}} </router-link></h1>
      <p v-show="post.userId">{{post.body | snippet}}</p>
      <p v-show="!post.userId">{{post.body}}</p>
      <button v-on:click.prevent='handleDelete(post.id)'>Delete</button>
    </div>
  </div>
</template>

<script>
export default {
  data: function() {
    return {
      posts: [],
      searchs: ""
    };
  },
  methods: {
      handleDelete(id){
          let newPosts = this.posts.filter(e=>{
              return e.id !== id
          });
          this.posts=newPosts
      }
  },
  computed: {
    SearchFilter: function() {
      return this.posts.filter(post => {
        return post.title.match(this.searchs.toLowerCase());
      });
    }
  },
  created: function() {
    this.$http.get("http://jsonplaceholder.typicode.com/posts").then(data => {
      this.posts = data.body.slice(0, 10);
    });
  },
  directives: {
    rainbow: {
      bind: function(el, binding, vnode) {
        el.style.color = "#" + String(Math.random() * 1000000).slice(0, 6);
        let a = 0;
        if (a === 3) {
          console.log(binding, vnode);
        }
      }
    }
  },
  filters:{
      snippet: function(value){
          return value.slice(0,100)+ ' ...>>>'
      }
  }
};
</script>

<style scoped>
h1 a{
    text-decoration: none;
}
div input[type="text"] {
  width: 50%;
  padding: 8px;
  display: block;
  margin: 8px auto;
}
div .post {
  width: 50%;
  height: auto;
  margin: 10px auto;
  border: 2px solid black;
  border-radius: 8px;
  background-color: white;
  padding: 15px 30px;
}
.post p {
  margin: 0;
  text-align: justify;
}
.post h1 {
  margin: 0;
}
.post button {
  padding: 8px;
  margin: 20px auto;
  display: block;
  background-color: aqua;
  border-radius: 3px;
  box-shadow: none;
  border: 3px solid white;
}
button:hover {
  background-color: blue;
  border: 3px solid violet;
  cursor: pointer;
}
button:focus {
  border: 3px solid violet;
}
</style>